import React, { useCallback, ChangeEvent, useState, useEffect } from 'react';
import { TextField, InputAdornment, makeStyles, Theme, Chip } from '@material-ui/core';
import { useDebounce } from 'hooks/useDebounce';
import Emoji from 'components/Utils/Emoji';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  textField: {
    width: '100%',
    marginBottom: spacing(2),
  },
  shortcuts: {
    margin: 0,
    padding: 0,
  },
  chip: {
    marginRight: spacing(1),
    marginBottom: spacing(0.5),
    lineHeight: 1.1,
  },
}));

interface Props {
  defaultValue?: string;
  shortcuts?: string[];
  onChange?: (value: string) => void;
  onClickShortcut?: (value: string) => void;
}
const SearchInput = ({ defaultValue = '', shortcuts = [], onChange = () => {}, onClickShortcut }: Props) => {
  const styles = useStyles();
  const [keyword, setKeyword] = useState(defaultValue);
  const emitChangeEvent = useDebounce(onChange, 500);
  const handleChangeValue = useCallback(
    (e: ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;
      setKeyword(value);
    },
    [setKeyword]
  );
  const handleClickCategoryShortcut = useCallback(
    (category: string) => {
      return () => {
        setKeyword(category);
        onClickShortcut?.(category);
      };
    },
    [setKeyword, onClickShortcut]
  );

  useEffect(() => {
    emitChangeEvent(keyword);
  }, [keyword, emitChangeEvent]);

  return (
    <div>
      <TextField
        placeholder="검색어를 입력하세요"
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <Emoji emoji="🔍" name="search" />
            </InputAdornment>
          ),
        }}
        value={keyword}
        onChange={handleChangeValue}
        className={styles.textField}
      />
      {shortcuts && (
        <ul className={styles.shortcuts}>
          {shortcuts.map(v => (
            <Chip
              key={v}
              size="small"
              label={v}
              variant="outlined"
              className={styles.chip}
              onClick={handleClickCategoryShortcut(v)}
            />
          ))}
        </ul>
      )}
    </div>
  );
};

export default SearchInput;
