import React, { useState, useMemo, useEffect, useCallback } from 'react';
import { useAllPosts } from 'hooks/useAllPosts';
import Layout from 'components/Layout';
import SEO from 'components/SEO';
import { Grid, makeStyles, Theme } from '@material-ui/core';
import { Link } from 'gatsby';
import SearchInput from 'components/Forms/SearchInput';
import { searchPost } from 'utils/searchPost';
import PostIntroCard from 'components/Cards/Posts';
import RowList from 'components/Lists/RowList';
import { generateLogger } from 'utils/loggers';
import { useAllCategories } from 'hooks/useAllCategories';

const logger = generateLogger('home_page');
const handlePostClick = (path: string) => {
  logger.click('click_posting', { path });
};
const handleSearchShortcutClick = (keyword: string) => {
  logger.click('click_search_shortcut', { keyword });
};

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  searchInput: {
    marginBottom: spacing(4),
  },
  items: {
    transition: 'transform 0.2s ease-in-out',
    '& a': {
      textDecoration: 'none',
    },
    '&:hover': {
      transform: 'scale(1.01)',
    },
  },
}));

const HomePage = () => {
  const styles = useStyles();
  const [keyword, setKeyword] = useState('');
  const allPosts = useAllPosts();
  const allCategories = useAllCategories();

  const posts = useMemo(() => {
    if (keyword.length < 2) {
      return allPosts;
    }

    return searchPost(keyword, allPosts);
  }, [allPosts, keyword]);

  const shortcuts = useMemo(() => {
    return allCategories.map(({ fieldValue }) => fieldValue);
  }, [allCategories]);

  const handleKeywordChange = useCallback((keyword: string) => {
    setKeyword(keyword);
    if (keyword.length > 1) {
      logger.event('search', 'search', { keyword });
    }
  }, []);

  useEffect(() => {
    logger.view();
  }, []);

  return (
    <Layout>
      <SEO />
      <div className={styles.searchInput}>
        <SearchInput
          defaultValue={keyword}
          shortcuts={shortcuts}
          onChange={handleKeywordChange}
          onClickShortcut={handleSearchShortcutClick}
        />
      </div>
      <RowList>
        {posts.map(p => (
          <Grid item xs={12} key={p.frontmatter.title} className={styles.items}>
            <Link to={p.fields.path} onClick={() => handlePostClick(p.fields.path)}>
              <PostIntroCard post={p} />
            </Link>
          </Grid>
        ))}
      </RowList>
    </Layout>
  );
};

export default HomePage;
